'use client'

import { Code2, Rocket, Users } from 'lucide-react'
import { motion } from 'framer-motion'
import { useInView } from 'framer-motion'
import { useRef } from 'react'

const stats = [
  { label: 'Projects Completed', value: '50+', icon: Rocket },
  { label: 'Years Experience', value: '5+', icon: Code2 },
  { label: 'Happy Clients', value: '30+', icon: Users },
]

export function About() {
  const ref = useRef(null)
  const isInView = useInView(ref, { once: true, margin: '-100px' })

  return (
    <section id="about" className="py-20 px-4 bg-muted/30" ref={ref}>
      <div className="container mx-auto max-w-6xl">
        <div className="space-y-12">
          <motion.div
            className="space-y-4"
            initial={{ opacity: 0, y: 20 }}
            animate={isInView ? { opacity: 1, y: 0 } : {}}
            transition={{ duration: 0.6 }}
          >
            <h2 className="text-3xl lg:text-4xl font-bold text-foreground">About Me</h2>
            <div className="h-1 w-20 bg-primary rounded-full" />
          </motion.div>

          <div className="grid lg:grid-cols-2 gap-8">
            <motion.div
              className="space-y-6"
              initial={{ opacity: 0, x: -50 }}
              animate={isInView ? { opacity: 1, x: 0 } : {}}
              transition={{ duration: 0.6, delay: 0.2 }}
            >
              <p className="text-lg text-muted-foreground leading-relaxed">
                I'm a passionate software engineer with expertise in building modern web applications. 
                My journey in tech started with curiosity and has evolved into a career focused on 
                creating impactful digital solutions.
              </p>
              <p className="text-lg text-muted-foreground leading-relaxed">
                I specialize in full-stack development, bringing ideas to life through clean, efficient code. 
                Whether it's crafting intuitive user interfaces or architecting robust backend systems, 
                I'm committed to delivering excellence in every project.
              </p>
            </motion.div>

            <div className="grid grid-cols-1 sm:grid-cols-3 lg:grid-cols-1 gap-6">
              {stats.map((stat, index) => {
                const Icon = stat.icon
                return (
                  <motion.div
                    key={stat.label}
                    initial={{ opacity: 0, y: 20 }}
                    animate={isInView ? { opacity: 1, y: 0 } : {}}
                    transition={{ duration: 0.6, delay: 0.3 + index * 0.1 }}
                    whileHover={{ scale: 1.05, borderColor: 'rgb(255, 107, 53)' }}
                    className="bg-card border border-border rounded-lg p-6 text-center space-y-2"
                  >
                    <Icon className="h-8 w-8 text-primary mx-auto" />
                    <p className="text-3xl font-bold text-foreground">{stat.value}</p>
                    <p className="text-sm text-muted-foreground">{stat.label}</p>
                  </motion.div>
                )
              })}
            </div>
          </div>
        </div>
      </div>
    </section>
  )
}
